import axios from 'axios';

import {
    AUTH_ERROR,
    GET_NEWS,
    FILTER_NEWS
} from './types';


export const getNews = (e) => async dispatch => {
    try {
            const res = await axios.get('https://newsapi.org/v2/top-headlines?' +
                                    'country=us&' +
                                    'pageSize=100&' +
                                    'apiKey=81ff8b3105cd407286c41a082f639c89');
                                    
             dispatch({
                type: GET_NEWS,
                payload: res.data.articles
             });
        
    } catch (err) {
        dispatch({
            type: AUTH_ERROR,
            payload: { msg: 'something went wrong' }
          });
          console.log(err)
    }
}
export const filterNewsByKeyword = (keyword) => async dispatch => {
    try {
        const res = await axios.get('https://newsapi.org/v2/everything?' +
          `q=${keyword}&` +
          'pageSize=100&' +
          'sortBy=relevancy&' +
          'apiKey=81ff8b3105cd407286c41a082f639c89');

          dispatch({
              type: GET_NEWS,
              payload: res.data.articles,
              outlet: keyword
          });

    } catch (err) {
        console.error(err);
        dispatch({
            type: AUTH_ERROR,
            payload: { msg: 'something went wrong' }
          });
    }
}
export const filterByBBC = () => async dispatch => {
    try {
        const res = await axios.get('httpss://newsapi.org/v2/everything?' +
                                'pageSize=100&' +
                                'sources=bbc-news&' +
                                'apiKey=81ff8b3105cd407286c41a082f639c89');

                                let link = document.getElementById('BBC').innerHTML;

                                dispatch({
                                   type: GET_NEWS,
                                   payload: res.data.articles,
                                   outlet: link
                                });
    
    } catch (err) {
        console.error(err);
        dispatch({
            type: AUTH_ERROR,
            payload: { msg: 'something went wrong' }
        });
    }
}
